.ss-profile-img {
  position: absolute;
  margin: 0 0 0 25%;
  bottom: -80px;
  left: 0;
  width: 180px;
  height: 180px;
  border: 8px solid #5D6D7E;
  border-radius: 50% 50%;
}

.header-lable-name {
  position: absolute;
  margin: 0 25% 0 50%;
}

.font-medium {
  font-weight: 500;
}

.text-2xl {
  font-size: 1.875rem;
}

.leading-normal {
  line-height: 1.5;
}

.pb-2 {
  padding-bottom: .5rem;
}

.text-white {
  --text-opacity: 1;
  color: #fff;
  color: rgba(255,255,255,var(--text-opacity));
}




@media (max-width: 768px) {
	.header-lable-name {
    position: absolute;
    margin: 0 5% 0 50%;
  }
}

@media (max-width: 350px) {
  .bgHero {
    position: relative;
    width: 100%;
    height: 260px;
  }
  .ss-profile-img {
    position: relative;
    display: table;
    bottom: -10%;
    left: 0;
    width: 120px;
    height: 120px;
    margin: 0 auto;
    border: 4px solid #adabab;
}
.header-lable-name {
  position: absolute;
  margin: 25px 0;
  display: inline-block;
  text-align: center;
}

.text-2xl {
  font-size: 1.5rem;
}

}


img {
  -webkit-transition: all .1s ease;
  -o-transition: all .1s ease;
  transition: all .1s ease;
  vertical-align: middle;
}

img:hover {
  -webkit-transform:scale(1.1); /* Safari and Chrome */ /* Firefox */
  -ms-transform:scale(1.1); /* IE 9 */ /* Opera */
  transform:scale(1.1);
}




